import React from 'react'
import axios from 'axios'
import InputPage from './input'
import { MDBBtn, MDBIcon } from "mdbreact";
import 'weather-icons/css/weather-icons.css'

import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
export default class App extends React.Component{
  constructor(){
    super()
    this.state={
      city:null,
      country:null,
      temp:null,
      min_temp:null,
      max_temp:null,
      icon:null
    
    }
    this.weatherIcon={
      Thunderstorm: "wi-thunderstorm",
      Drizzle: "wi-sleet",
      Rain: "wi-storm-showers",
      Snow: "wi-snow",
      Atmosphere: "wi-fog",
      Clear: "wi-day-sunny",
      Clouds: "wi-day-fog"
    }
  }
  cel(data){
    var temp=Math.floor(((Math.floor(data-237.15))-32)*5/9)
   return temp
 }
 icon(data1,data2){
   switch (true) {
    case data2 >= 200 && data2 < 232:
      this.setState({ icon: data1.Thunderstorm });
      break;
    case data2 >= 300 && data2 <= 321:
      this.setState({ icon: data1.Drizzle });
      break;
    case data2 >= 500 && data2 <= 521:
      this.setState({ icon: data1.Rain });
      break;
    case data2 >= 600 && data2 <= 622:
      this.setState({ icon: data1.Snow });
      break;
    case data2 >= 701 && data2 <= 781:
      this.setState({ icon: data1.Atmosphere });
      break;
    case data2 === 800:
      this.setState({ icon: data1.Clear });
      break;
    case data2 >= 801 && data2 <= 804:
      this.setState({ icon: data1.Clouds });
      break;
    default:
      this.setState({ icon: data1.Clouds });
  }
 }
  run(){
  
    const city=document.getElementById("city").value
    const country=document.getElementById('country').value
    axios.get(`https://cors-anywhere.herokuapp.com/api.openweathermap.org/data/2.5/weather?q=${city},${country}&appid=429736441cf3572838aa10530929f7cd`)
  .then(res=>{
    console.log(res.data.main.temp,res.data.main.temp_min,res.data.main.temp_max)
        this.setState({
          temp:this.cel(res.data.main.temp),
          min_temp:this.cel(res.data.main.temp_min),
          max_temp:this.cel(res.data.main.temp_max),
          icon:res.data.weather[0].id

        })
        this.icon(this.weatherIcon,this.state.icon)
        console.log(this.state)

  })}
  render(){
 
    return(
      <>
      <div className='container'>
        <div className="row">
          <div className='col-sm-6'>
    <InputPage  label="Your City" id="city"/>
    </div>
    <div className='col-sm-6'>
    <InputPage  label="Your Country" id="country"/>
    </div>
  <div className='col-sm-12' >
   <MDBBtn  className='text-center' tag="a" size="lg" floating gradient={(true?"purple":null).toString()}  onClick={()=>{
     this.run()
   }}>
        <MDBIcon icon="bolt" />
      </MDBBtn> 
      </div>
{this.state.max_temp!=null?
<>


<div className="container">
  <div className="row">
    <div className="col-12 text-center" style={{marginTop:"20px !important"}}><i className={`wi ${this.state.icon} display-1`}/></div>
    <div className="col-12 text-center">
    <h1>{this.state.temp}&deg;C</h1>
      </div>
      <div className="col-6 text-right">
      <h1>{this.state.min_temp}&deg;C</h1>
        </div>
        <div className="col-6">
      <h1>{this.state.max_temp}&deg;C</h1>
        </div>
    </div>
  </div>
</>
:null


}
</div>
     </div> 
      </>
    )
  }
}